import { Injectable } from '@angular/core';
import { ChallengeResult } from 'src/app/__models/challenge.result';

@Injectable({
  providedIn: 'root'
})
export class Challenge {
  private startTime: number;
  private challengeInfo: ChallengeInfo;

  constructor() { }

  start(challengeInfo: ChallengeInfo) {
    this.challengeInfo = challengeInfo;
    this.startTime = window.performance.now();
  }

  finish(): ChallengeResult {
    const finishTime = window.performance.now();
    const result: ChallengeResult = {
      miliseconds: finishTime - this.startTime,
      typedCharacters: this.challengeInfo.charactersToType,
      typedWords: this.challengeInfo.wordsToType,
    }
    return result;
  }
}

export interface ChallengeInfo {
  charactersToType: number;
  wordsToType: number;
}
